

.home {
    color: #333333;
    text-align: center;
    top: 0;
    left: 0;
    -ms-flex-item-align: start;
        align-self: flex-start;

    position: absolute;
    right: 0;
    bottom: 0;
    margin-top: 10%;

    height:80%;
}

.home-content {
    display: -ms-flexbox;
    display: flex;
    border: 1px solid #d2d2d2;
    -ms-flex: 1 1;
        flex: 1 1;
    margin-bottom: 20px;
    position: relative;
}

.app {
    display: -ms-flexbox;
    display: flex;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.app-logo {
    -webkit-animation: app-logo-spin infinite 20s linear;
            animation: app-logo-spin infinite 20s linear;
    height: 450px;
}


.app-title {
    font-size: 4rem;
}

.app-intro {
    font-size: large;
}

@-webkit-keyframes app-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes app-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



